<template>
    <div class="ResumeMain">
        <!-- 个人介绍 -->
        <div class="PersonalIntroduction">
            <div class="HeadPortrait">
                <div class="HeadPortraitImg2">
                    <div class="ResumeCHJOOLogo">
                        <div id="ResumeREye">
                            <div id="ResumeOrbitalCavityR">
                                <div id="ResumeEyeballR">
                                </div>
                            </div>
                        </div>
                        <div id="ResumeLEye">
                            <div id="ResumeOrbitalCavityL">
                                <div id="ResumeEyeballL">
                                </div>
                            </div>
                        </div>
                        <div class="ResumeMouth">
                        </div>

                    </div>
                    <div class="ResumelogoBody">

                    </div>
                </div>
            </div>
            <div class="Introduce">
                <p class="IntroduceText">
                    <span class="IntroduceTextChild1"> CHJOO(曾永键)</span><br>
                    <span class="IntroduceTextChild2">程序员 <span class="IntroduceTextChild2Mini">&</span>
                        想象艺术家</span><br>
                    <span
                        class="IntroduceTextChild3">你好！我叫曾永键，今年21岁，是一名刚毕业于软件开发专业的应届生，大学期间热衷于前端技术的开发。我熟练掌握HTML5、CSS3、JavaScript及Vue3，目前正学习Cocos
                        creator。</span><br>
                    <span
                        class="IntroduceTextChild3">我对响应式设计和可访问性有着深刻的理解，能够确保网站在各种设备上都能提供出色的用户体验。因此，我正在积极寻找一段长期的前端开发工作机会。</span>
                </p>
            </div>
        </div>
        <!-- 技术 -->
        <div class="Working">
            <!-- 软件 -->
            <div class="SoftwareTitleBox">
                <Triangle msg="1" class="Triangle"></Triangle>
                <p class="SoftwareTitle2">熟练技术</p>
            </div>
            <div id="WorkingBox">
                <li class="WorkingBoxChildren" v-for="(item, value) in WorkingData" :key="value"
                    :class="{ 'IdentifyData': value == WorkingDataNow }" @click="ClickOnSkills(value)">
                    {{ item }}
                </li>
            </div>
        </div>
        <!-- 技术细节 -->
        <div class="TechnicalDetails">
            <p class="TechnicalDetailsTitle">-{{ WorkingData[WorkingDataNow] }}-</p>
            <div class="TechnicalDetailsR">
                <p class="TechnicalDetailsDataTitle">业务能力</p>
                <li v-for="(items, index) in BusinessCapabilityNow" :key="index">
                    {{ items }}
                </li>
            </div>
            <div class="TechnicalDetailsL">
                <p class="TechnicalDetailsDataTitle">技术栈</p>
                <li v-for="(items, index) in TechnologyStackNow" :key="index">
                    {{ items }}
                </li>
            </div>
        </div>
        <!-- logo -->
        <div class="About">
            <img src="@assets/img/logo.png" alt="" class="AboutLogo">
            <p class="AboutText">Copyright © 2023-2024 CHJOO runoob.com All Rights Reserved.</p>
        </div>
        <!-- 工作 -->
        <div class="Software">
            <!-- 工作 -->
            <div class="SoftwareTitleBox">
                <Triangle msg="1" class="Triangle"></Triangle>
                <p class="SoftwareTitle">工作经历</p>
            </div>
            <div class="WorkExperience" v-for="(item, index) in SoftwareTitle" :key="index">
                <p class="WorkExperienceName">
                    <li class="WorkExperienceNameLi">{{ item.name }}</li>
                    <li class="WorkExperienceNameLi2">{{ item.briefIntroduction }}</li>
                </p>
            </div>
        </div>
        <!-- 时间线 -->
        <div class="EventLine">

            <div id="RulerOverall">
            </div>
            <div class="SmallScaleRuler" v-for="(item, index) in RulerLength" :key="index">

            </div>
            <p class="EventLineTitle">校园经历</p>
            <div class="EventLineData" v-for="(item, index) in TimelineData" :key="index">
                <div class="EventLineDataName">
                    <div class="EventLineDataNameMini"></div>
                    <p>{{ item.name }}</p>
                </div>
                <li class="EventLineDataData" v-for="(item, index) in item.data" :key="index">
                    {{ item }}
                </li>
            </div>

        </div>
        <!-- 联系方式 -->
        <div class="ContactInformation">
            <p class="SoftwareTitle3">请联系我</p>
            <div class="ContactSubmodule" v-for="(item, index) in ContactInformationData" :key="index"
                @click="ContactPhone(index)">
                <img :src=item.img alt="" class="ContactLogo">
                <div class="ContactAddress">
                    <div :class="{ 'ContactBlank': isEven(index), 'ContactBlank2': !isEven(index) }"></div>
                    <p :class="{ 'ContactUrl': isEven(index), 'ContactUrl2': !isEven(index) }">{{ item.url }}</p>
                </div>
            </div>
            <div class="ContactInformationPhoneBand"></div>
            <div class="ContactInformationPhoneText">{{ ContactPhoneNow }}</div>
        </div>
    </div>
</template>
<script setup>
import { onMounted, ref, onUnmounted } from "vue";
import Triangle from '@/components/Triangle.vue';
import { useStore } from "vuex";
// 技术数据
const store = useStore();
const TechnicalDataDetails = store.state.TechnicalDataDetails;
// 联系览数据
const ContactInformationData = ref(store.state.ContactInformationData)
const SoftwareTitle = ref(store.state.SoftwareTitle)
const TimelineData = ref(store.state.TimelineData)
const WorkingData = ref(store.state.WorkingData)
const RulerLength = 41
const WorkingDataNow = ref(0)
const TechnologyStackNow = ref([])
const BusinessCapabilityNow = ref([])
const TimelineDataNow = ref(true)
const ContactPhoneNow = ref("")
const ContactPhoneNum = ref(ContactInformationData.value.length - 1)
// 判断索引是否为偶数的函数
function isEven(index) {
    return index % 2 === 0;
}
// 横竖屏检测
function ScreenRecognition() {
    const isportrait = window.matchMedia('(orientation: portrait)').matches
    // console.log(isportrait)
    if (isportrait) {
        if (TimelineDataNow.value) {
            TimelineData.value.reverse()
            const ResumeMain = document.getElementsByClassName("ResumeMain")
            ResumeMain[0].style.marginTop = "0px"
        }
        TimelineDataNow.value = false
        ContactPhone(ContactPhoneNum.value)
    } else {
        if (TimelineDataNow.value == false) {
            TimelineData.value.reverse()
        }
        TimelineDataNow.value = true
        // console.log("横屏");
        ScreenPc()
        SetTheScale()
        TechnicalLocation()
        ScreenPc()
    }

    // console.log(TimelineDataNow)
}
function ScreenPc() {
    const ResumeMain = document.getElementsByClassName("ResumeMain")
    // console.log("ResumeMain:", ResumeMain[0]);
    var width = ref(window.innerWidth)
    var height = ref(window.innerHeight)
    const IsWidth = 1912
    const IsHeight = 956
    const NewHeight = (width.value * IsHeight) / IsWidth
    const NewWidth = (height.value * IsWidth) / IsHeight
    if (width.value > NewWidth) {
        ResumeMain[0].style.width = width.value + "px"
        ResumeMain[0].style.height = height.value + "px"
        ResumeMain[0].style.minHeight = NewHeight + "px"
        // console.log("if")   
    }
    else {
        ResumeMain[0].style.width = width.value + "px"
        ResumeMain[0].style.height = NewHeight + "px"
        ResumeMain[0].style.minHeight = "0px"
        ResumeMain[0].style.marginTop = (height.value - NewHeight) / 2 + "px"
        // console.log("else")  
    }
    // console.log("现在的", width.value, ":", height.value)
    // console.log("测试的", NewWidth, ":", NewHeight);
}
function ContactPhone(index) {
    ContactPhoneNum.value = index
    const ContactLogo = document.getElementsByClassName("ContactLogo")
    const ContactInformationPhoneBand = document.getElementsByClassName("ContactInformationPhoneBand")[0]
    const Logoleft = ContactLogo[index].getBoundingClientRect().left
    ContactPhoneNow.value = ContactInformationData.value[index].url
    ContactInformationPhoneBand.style.left = Logoleft + "px"
}
// 渲染尺子尺度-Phone
function SetTheScalePhone() {
    var SmallScaleRuler = document.getElementsByClassName("SmallScaleRuler2")
    var EventLineDataName = document.getElementsByClassName("EventLineDataName")
    var EventLineTitle = document.getElementsByClassName("EventLineTitle")
    var EventLineTitleTop = EventLineTitle[0].getBoundingClientRect().top
    for (var i = 0; i < EventLineDataName.length; i++) {
        var EventLineDataNameTop = EventLineDataName[i].getBoundingClientRect().top
        // console.log("现在查看：", EventLineDataNameTop);
        SmallScaleRuler[i].style.top = (EventLineDataNameTop - EventLineTitleTop) + "px"
    }
}
// 渲染尺子尺度-PC
function SetTheScale() {
    var RulerOverall = document.getElementById("RulerOverall")
    var SmallScaleRuler = document.getElementsByClassName("SmallScaleRuler")
    var RulerOverallWidth = parseFloat(window.getComputedStyle(RulerOverall).getPropertyValue("width"))
    var left = RulerOverall.getBoundingClientRect().left
    var Interval = (RulerOverallWidth) / (RulerLength - 1)
    // 数据
    var EventLineData = document.getElementsByClassName("EventLineData")
    var EventLineLen = 0
    // console.log(left);
    for (var i = 0; i < SmallScaleRuler.length; i++) {
        if ((i) % 10 == 0 || i == 0) {
            SmallScaleRuler[i].style.width = "0.3rem"
            SmallScaleRuler[i].style.height = "1.5rem"
            SmallScaleRuler[i].style.top = "-4%"
        } else if ((i) % 5 == 0) {
            var EventLineWidth = parseFloat(window.getComputedStyle(EventLineData[EventLineLen]).getPropertyValue("width"))
            SmallScaleRuler[i].style.width = "1rem"
            SmallScaleRuler[i].style.height = "1rem"
            SmallScaleRuler[i].style.top = "-1%"
            SmallScaleRuler[i].style.borderRadius = "50%"
            SmallScaleRuler[i].style.background = "#ECD1A2"
            SmallScaleRuler[i].style.border = "0.2rem solid #0a40c6"
            EventLineData[EventLineLen].style.left = (left - EventLineWidth / 2) + "px"
            EventLineData[EventLineLen].style.top = "15%"
            EventLineLen += 1
        }
        var SmallScaleRulerWidth = parseFloat(window.getComputedStyle(SmallScaleRuler[i]).getPropertyValue("width"))
        SmallScaleRuler[i].style.left = (left - SmallScaleRulerWidth / 2) + "px"
        left += Interval
    }
}
//渲染技术
function TechnicalLocation() {
    var WorkingBoxChildren = document.getElementsByClassName("WorkingBoxChildren")
    if (WorkingData.value.length > 10) {
        WorkingData.value = WorkingData.value.slice(0, 10)
    }
    // console.log(WorkingData.value.length)
    for (var i = 0; i < WorkingData.value.length; i++) {
        if (i % 2 == 0) {
            // console.log(i)
            WorkingBoxChildren[i].style.gridColumnStart = 1
            WorkingBoxChildren[i].style.gridColumnEnd = 2
        } else {
            WorkingBoxChildren[i].style.gridColumnStart = 2
            WorkingBoxChildren[i].style.gridColumnEnd = 3
        }
    }
}
// 技术点击事件
function ClickOnSkills(index) {
    WorkingDataNow.value = index
    const NowData = WorkingData.value[WorkingDataNow.value]
    const TechData = TechnicalDataDetails[NowData]
    TechnologyStackNow.value = TechData.technologies
    BusinessCapabilityNow.value = TechData.capabilities
}
// Logo循环事件
const timer = ref(null);
const ResumeEyeballInitialization = ref(1)
const ResumeEyeballBlooe = ref(true)
const loopFunction = () => {
    // console.log('循环事件触发');
    // 在这里放置你的业务逻辑
    const ResumeEyeballR = document.getElementById("ResumeEyeballR")
    const ResumeEyeballL = document.getElementById("ResumeEyeballL")
    const ResumelogoBody = document.getElementsByClassName("ResumelogoBody")[0]
    const ResumeCHJOOLogo = document.getElementsByClassName("ResumeCHJOOLogo")[0]
    const ResumeMouth = document.getElementsByClassName("ResumeMouth")[0]
    function getRandomFloatInclusive(min, max) {
        return Number(Math.random() * (max - min + Number.EPSILON) + min).toFixed(2);
    }
    if (ResumeEyeballBlooe.value) {
        ResumeEyeballR.style.left = getRandomFloatInclusive(0, 1.2) + "rem"
        ResumeEyeballR.style.top = getRandomFloatInclusive(0, 1.2) + "rem"
        ResumeEyeballL.style.left = getRandomFloatInclusive(0, 1.2) + "rem"
        ResumeEyeballL.style.top = getRandomFloatInclusive(0, 1.2) + "rem"
        ResumelogoBody.style.width = "20rem"
        ResumelogoBody.style.height = "20rem"
        ResumelogoBody.style.top = "0rem"
        ResumeCHJOOLogo.style.top = "3rem"
        ResumeCHJOOLogo.style.borderRadius = "3rem"
        ResumeCHJOOLogo.style.width = "9rem"
        ResumeCHJOOLogo.style.height = "9rem"
        ResumeMouth.style.width = "60%"
        ResumeMouth.style.height = "30%"
        ResumeEyeballBlooe.value = false
    } else {
        ResumeEyeballR.style.left = ResumeEyeballInitialization.value + "rem"
        ResumeEyeballR.style.top = ResumeEyeballInitialization.value + "rem"
        ResumeEyeballL.style.left = ResumeEyeballInitialization.value + "rem"
        ResumeEyeballL.style.top = ResumeEyeballInitialization.value + "rem"
        ResumelogoBody.style.width = "13rem"
        ResumelogoBody.style.height = "13rem"
        ResumelogoBody.style.top = "2.5rem"
        ResumeCHJOOLogo.style.top = "4.2rem"
        ResumeCHJOOLogo.style.borderRadius = "50%"
        ResumeCHJOOLogo.style.width = "8rem"
        ResumeCHJOOLogo.style.height = "8rem"
        ResumeMouth.style.width = "50%"
        ResumeMouth.style.height = "20%"
        ResumeEyeballBlooe.value = true
    }
};

// 启动定时器
const startTimer = () => {
    timer.value = setInterval(loopFunction, 5000); // 每5秒执行一次
};

// 清除定时器
const clearTimer = () => {
    if (timer.value) {
        clearInterval(timer.value);
    }
};
// 生命周期
onMounted(() => {
    ClickOnSkills(WorkingDataNow.value)
    ScreenRecognition()
    startTimer()
    window.addEventListener('resize', ScreenRecognition);
});

onUnmounted(() => {
    clearTimer()
    window.removeEventListener('resize', ScreenRecognition);
});
</script>
<style scoped>
@import '@assets/css/ResumePC.css';
@import '@assets/css/ResumePhone.css';
@import '@assets/css/Font.css';
</style>
